'use client';

import { useState } from 'react';
import { Input } from '@/components/ui/Input';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';

export default function RendaFamiliar() {
  const [salarioMichael, setSalarioMichael] = useState('');
  const [salarioFernanda, setSalarioFernanda] = useState('');
  const [outrasMichael, setOutrasMichael] = useState('');
  const [outrasFernanda, setOutrasFernanda] = useState('');
  const [gastos, setGastos] = useState('');
  const [resultado, setResultado] = useState<{
    totalEntradas: number;
    saldoFinal: number;
  } | null>(null);

  const formatarReal = (valor: number | string) =>
    new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL',
    }).format(Number(valor));

  const calcular = () => {
    const sm = Number(salarioMichael);
    const sf = Number(salarioFernanda);
    const om = Number(outrasMichael);
    const of = Number(outrasFernanda);
    const g = Number(gastos);

    if ([sm, sf, om, of, g].some((v) => isNaN(v))) {
      alert('Preencha todos os campos corretamente!');
      return;
    }

    const totalEntradas = sm + sf + om + of;
    const saldoFinal = totalEntradas - g;

    setResultado({ totalEntradas, saldoFinal });
  };

  const limpar = () => {
    setSalarioMichael('');
    setSalarioFernanda('');
    setOutrasMichael('');
    setOutrasFernanda('');
    setGastos('');
    setResultado(null);
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-pink-100 to-white p-4 flex items-center justify-center">
      <div className="w-full max-w-xl">
        <Card>
          <h1 className="text-3xl font-bold text-center mb-6">Renda Familiar</h1>

          <div className="grid gap-4 md:grid-cols-2">
            <Input
              type="number"
              placeholder="SalÃ¡rio Michael"
              value={salarioMichael}
              onChange={(e) => setSalarioMichael(e.target.value)}
            />
            <Input
              type="number"
              placeholder="SalÃ¡rio Fernanda"
              value={salarioFernanda}
              onChange={(e) => setSalarioFernanda(e.target.value)}
            />
            <Input
              type="number"
              placeholder="Outras Entradas Michael"
              value={outrasMichael}
              onChange={(e) => setOutrasMichael(e.target.value)}
            />
            <Input
              type="number"
              placeholder="Outras Entradas Fernanda"
              value={outrasFernanda}
              onChange={(e) => setOutrasFernanda(e.target.value)}
            />
            <Input
              type="number"
              placeholder="Gastos Familiares"
              value={gastos}
              onChange={(e) => setGastos(e.target.value)}
              className="md:col-span-2"
            />
          </div>

          <div className="mt-6 flex flex-col md:flex-row items-center justify-center gap-4">
            <Button onClick={calcular} className="w-full md:w-auto text-lg px-6 py-2">
              Calcular
            </Button>
            <Button
              onClick={limpar}
              className="w-full md:w-auto text-lg px-6 py-2 bg-gray-300 text-gray-800 hover:bg-gray-400"
            >
              Limpar
            </Button>
          </div>

          {resultado && (
            <div className="mt-8 space-y-4 text-center">
              <div className="bg-blue-100 p-4 rounded-xl">
                <p className="text-lg font-medium">
                  ðŸ“¥ Total de Entradas: <strong>{formatarReal(resultado.totalEntradas)}</strong>
                </p>
              </div>
              <div className="bg-green-100 p-4 rounded-xl">
                <p className="text-lg font-medium">
                  ðŸ§¾ Saldo Final: <strong>{formatarReal(resultado.saldoFinal)}</strong>
                </p>
              </div>
            </div>
          )}
        </Card>
      </div>
    </main>
  );
}
